<template>
  <div id="songDetails">
    <!-- navigation header -->
    <div class="navigationHeader">
      <router-link @click.native="goback()" to="">
        <img src="/static/img/left-arrow-key.svg" alt="" width="20px"> Back
      </router-link>
    </div>
    <!-- Navigation ends here -->
    <div class="row align-items-center" style="height:100%">
      <div class="col holder">
        <div class="songDetails">
          <img class="coverImage" :src="coverImage" alt="" width="150px" height="150px">
          <h2>{{ songName }}</h2>
          <p>{{ artist }}</p>
          <button type="button" name="button">Download</button>
        </div>

      </div>
    </div>

  </div>
</template>
<script>
export default {
  name: "songDetails",
  data: () => ({
    songName : "Pink Lips",
    artist : "Sunny Leone",
    coverImage : "https://advicesacademy.com/wp-content/uploads/2015/11/Sunny-Leone-Smile.jpg",
    fileURL : "https://dl.jatt.link/hd1.jatt.link/6f1813a8c4bb6bce2257cb54031551de/ucjzv/Hathyar-(Mr-Jatt.com).mp3"
  }),
  methods: {
    goback() {
      this.$router.go(-1);
    }
  }
}
</script>
<style lang="scss" scoped>
#songDetails{
  top: 0; left: 0; position: fixed;
  background-color: #4286f5;
  width: 100%; z-index: 2;
  height: 100%;

  .navigationHeader{
    padding: 10px;

    a{
      color: #fff;
    }

    img{
      vertical-align: bottom;
    }
  }
  .holder{
    margin: 0 10px;
    color: #fff;
    text-align: center;

    .songDetails{
      .coverImage{
        border-radius: 50%;
      }
      h2{
        margin: 5px 0;
      }
      p{
        margin: 5px 0;
        color : rgba(255,255,255,0.7);
      }
      button{
        background-color: #fff;
        border-radius: 3px;
        padding: 10px 20px;
        color: #4286f5;
        border : none;
        margin-top: 12px;
        font-size: 14px;
      }
    }
  }
}
</style>



